import React, { useRef } from 'react';
import { Auth } from '../../Api'
import { useHistory } from "react-router"
import './main.css'
import "./util.css"
import "bootstrap"

async function envia(history, props) {
    const PayLoadLogin = JSON.stringify({
        'login': document.getElementById('login').value,
        'senha': document.getElementById('senha').value
    })

    const retorno = await Auth(history, PayLoadLogin)
    if (retorno != undefined)
        props.kk(retorno)
}


export default (props) => {
    let history = useHistory()
    const inputEl = useRef(null);

    return (
        <div>
            <form className="limiter" onSubmit={(event) => {
                event.preventDefault();
                envia(history, props)
            }}>
                <div className="container-login100">
                    <div className="wrap-login100">
                        <div className="login100-form validate-form">
                            <div className="alert alert-danger" role="alert" id="login_fail" style={{
                                display: "none"
                                // display: "block"
                            }}>
                                Usuário e senha incorretos..
                            </div>
                            <span className="login100-form-title p-b-26">
                                <img

                                    ref={inputEl}
                                    src={`${process.env['REACT_APP_BASE_URL3']}imagem/logo.jpeg`}
                                    style={{ padding: 20, borderRadius: 25 }}
                                />
                            </span>
                            <span className="login100-form-title p-b-48">
                                <i className="zmdi zmdi-font"></i>
                            </span>

                            <div className="wrap-input100 validate-input" data-validate="Valid email is: a@b.c">
                                <input className="input100" type="text" id="login" name="email" placeholder="Usuário" />
                            </div>

                            <div className="wrap-input100 validate-input" data-validate="Enter password">
                                <span className="btn-show-pass">
                                    <i className="zmdi zmdi-eye"></i>
                                </span>
                                <input className="input100" id="senha" type="password" name="pass" placeholder="Senha" />
                            </div>

                            <div className="container-login100-form-btn">
                                <div className="wrap-login100-form-btn">
                                    <div className="login100-form-bgbtn"></div>
                                    <button className="login100-form-btn" style={{
                                        backgroundColor: 'blue'
                                    }}>
                                        ENTRAR
                                    </button>
                                </div>
                            </div>


                        </div>
                    </div>
                </div>
            </form>
        </div>
    );
};